@import url("https://fonts.googleapis.com/css?family=Abel");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.TechIcon {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .TechIcon--icon {
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease; }
    .TechIcon--icon:hover {
      transform: scale(1.1); }
  .TechIcon--subtitle {
    text-align: center;
    color: #111; }

@media screen and (min-width: 0px) {
  .TechIcon {
    width: 3rem;
    margin: 1rem; }
    .TechIcon--icon {
      width: 3rem;
      height: 3rem; }
    .TechIcon--subtitle {
      display: none; } }

@media screen and (min-width: 500px) {
  .TechIcon {
    width: 4rem;
    margin: 1.2rem; }
    .TechIcon--icon {
      width: 4rem;
      height: 4rem; }
    .TechIcon--subtitle {
      display: block;
      margin-top: 0rem; } }
