@import url("https://fonts.googleapis.com/css?family=Abel");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.AboutMe {
  padding-bottom: 5rem; }
  .AboutMe--heading {
    font-size: 4.5rem;
    font-weight: 400;
    margin: 3rem 0 0;
    line-height: 1; }
  .AboutMe--subHeading {
    font-weight: bold;
    margin: 1rem 0;
    line-height: 1.5;
    margin: 1.5rem 0; }
  .AboutMe--text-secondaryColor {
    color: #ff4136; }
  .AboutMe--main {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-gap: 0rem 1rem;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center; }
  .AboutMe--gridHeading {
    grid-area: gh;
    justify-self: left;
    margin: 0; }
  .AboutMe--image {
    grid-area: portrait;
    margin: 0 auto; }
  .AboutMe--bio {
    font-size: 1.2rem;
    grid-area: bio;
    text-align: left; }

@media screen and (min-width: 0px) {
  .AboutMe--main {
    grid-template-columns: 1fr;
    grid-template-areas: "portrait" "gh" "bio"; }
  .AboutMe--image {
    font-size: 7rem; } }

@media screen and (min-width: 768px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, minmax(10px, 1fr));
    grid-template-areas: ". gh ." "portrait bio bio"; }
  .AboutMe--image {
    font-size: 12rem; } }

@media screen and (min-width: 1171px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: ". gh ." "portrait bio bio"; } }
